@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

body{
  margin: 0;
}

.top-pane{
  background-color: hsl(225, 6%,25%);
}

.pane{
  height: 50vh;
  display: flex;
}

.pane2{
  height: 40vh;
}

.editor-container{
  flex-grow: 1;
  flex-basis: 0;
  display: flex;
  flex-direction: column;
  padding: .5rem;
  background-color: hsl(225, 6%,25%);
}

.editor-title{
  display: flex;
  justify-content: space-between;
  background-color: hsl(225, 6%,13%);
  color: white;
  padding: .5rem .5rem .5rem 1rem;
  border-top-right-radius: .5rem;
  border-top-left-radius: .5rem;
}

.CodeMirror{
  height: 100% !important;
}



.code-mirror-wrapper{
  flex-grow: 1;
  border-bottom-right-radius: .5rem;
  border-bottom-left-radius: .5rem;
  overflow: hidden;
}

.editor-container.collapsed{
  flex-grow: 0;
}

.editor-container.collapsed .CodeMirror-scroll {
  position: absolute;
  overflow: hidden !important;
}

button{
  border: none;
  outline: none;
  background: none;
  color: white;
  cursor: pointer;
  font-size: 20px;
}

.editor-title{
  text-align: center;
  /* justify-content: center; */
  font-size: 20px;
  font-family: Poppins;
}

.navbar{
  width: 100%;
  display: flex;
  font-size: 20px;
  font-family: Poppins;
  background-color: hsl(225, 6%,25%);
}
.logo{
  width: 50%;
  margin-left: 10px;
  font-weight: 700;
  color: white;
  cursor: pointer;
}

.right{
  margin-top: 8px;
  text-align: center;
  display: flex;
  justify-content: center;
  margin-left: 30%;
  cursor: pointer;
}

.right-git{
  color: black;
  text-align: center;
  margin-right: 20px;
}

.right-lin{
  color: white;
  background:#0077b5 ;
  /* border-radius: 50%; */
}